<template>
	<div>
		<div class="con-btn" @click="hide">

<!--			{{ show }}-->
x
			<div class="circle-btn" v-if="title == null" @click.stop="showHide">
				<div class="cir"></div>
				<div class="cir"></div>
				<div class="cir"></div>
			</div>

			<div class="simple-btn" :class="mode" v-if="title != null" @click.stop="showHide">{{ title }}</div>

			<div class="my-box" ref="box">
				<div class="items" ref="items">
					<div v-for="(a,index) of options" :key="index" class="item">{{ a }}</div>
				</div>
			</div>
		</div>


		<p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aperiam asperiores cumque cupiditate dolore
			doloremque doloribus ea eveniet exercitationem inventore ipsa, iste mollitia nihil, pariatur perferendis
			ratione recusandae rerum similique voluptatem?</p>
	</div>
</template>

<script>

    export default {
        name: 'stripe-menu',
        data() {
            return {
                show: false,

            }
        },

        props: {

            options: {type: Array},

            title: String,

            mode: String

        },


        methods: {
            showHide() {
                if (!this.show) {
                    // let box = document.querySelector('.my-box');
                    // let items = document.querySelector('.items');
                    let box = this.$refs.box;
                    let items = this.$refs.items;
                    let h = items.offsetHeight;
                    // let w = items.offsetWidth;
                    box.style.height = h + 'px';
                    // box.style.width = w + 'px';
                } else {
                    let box = document.querySelector('.my-box');
                    box.style.height = '0';
                    // box.style.width = '0';

                }
                this.show = !this.show;

            },

            hide() {
                this.show = false;
                let box = document.querySelector('.my-box');
                box.style.height = '0';
            }

        },
    }

</script>

<style scoped lang="scss">

	.con-btn {
		position: relative;


		.simple-btn {
			position: relative;
			top: 0;
			left: 0;
		}

		.circle-btn {
			width: 40px;
			height: 40px;
			border-radius: 50%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			cursor: pointer;
			transition: all .5s;

			&:hover {
				background-color: rgba(0, 0, 0, .2);

			}

			.cir {
				width: 5px;
				height: 5px;
				border-radius: 50%;
				background-color: #000000;
				margin: 1px;
			}


		}


		.my-box {
			width: auto;
			height: 0;
			background-color: orange;
			overflow: hidden;
			transition: all .5s;
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			top: 44px;
			left: 20px;

		}


		/*.show {*/
		/*	width: 100vw;*/
		/*	height: 100vh;*/
		/*}*/

		.items {
			background-color: pink;
			text-align: left;
			/*width: 100%;*/

			.item {
				transition: background-color .5s;
				user-select: none;
				padding: 10px 30px;

				&:hover {
					background-color: yellow;
					display: block;
				}
			}
		}


	}

</style>